'use client';

import { useState, useRef } from 'react';
import Link from 'next/link';
import { saveAnalysis, generateId } from '@/lib/storage';
import type { VideoAnalysis } from '@/lib/storage';

export default function Dashboard() {
  const [file, setFile] = useState<File | null>(null);
  const [uploading, setUploading] = useState(false);
  const [analysis, setAnalysis] = useState<VideoAnalysis | null>(null);
  const [error, setError] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile && selectedFile.type.startsWith('video/')) {
      setFile(selectedFile);
      setError(null);
      setAnalysis(null);
    } else {
      setError('Please select a valid video file');
    }
  };

  const extractFrameFromVideo = (videoFile: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const video = document.createElement('video');
      video.preload = 'metadata';
      video.muted = true;
      video.playsInline = true;
      
      video.onloadedmetadata = () => {
        // Seek to middle of video for better frame
        video.currentTime = Math.min(video.duration / 2, 1);
      };
      
      video.onseeked = () => {
        try {
          const canvas = document.createElement('canvas');
          canvas.width = video.videoWidth;
          canvas.height = video.videoHeight;
          const ctx = canvas.getContext('2d');
          
          if (!ctx) {
            reject(new Error('Could not get canvas context'));
            return;
          }
          
          ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
          const frameDataUrl = canvas.toDataURL('image/jpeg', 0.8);
          resolve(frameDataUrl);
        } catch (err) {
          reject(err);
        }
      };
      
      video.onerror = (err) => {
        reject(new Error('Failed to load video'));
      };
      
      video.src = URL.createObjectURL(videoFile);
    });
  };

  const handleUpload = async () => {
    if (!file) {
      setError('Please select a video file first');
      return;
    }

    setUploading(true);
    setError(null);
    setAnalysis(null);

    try {
      // Extract a frame from the video for analysis
      let frameImage: string | null = null;
      try {
        frameImage = await extractFrameFromVideo(file);
      } catch (frameError: any) {
        console.warn('Could not extract frame, will try sending video directly:', frameError);
        // Continue with video file if frame extraction fails
      }

      const formData = new FormData();
      formData.append('video', file);
      if (frameImage) {
        formData.append('frame', frameImage);
      }

      const response = await fetch('/api/analyze-video', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        let errorMessage = 'Failed to analyze video';
        try {
          const errorData = await response.json();
          errorMessage = errorData.error || errorData.details || errorMessage;
          if (errorData.details) {
            console.error('API Error details:', errorData.details);
          }
        } catch (e) {
          // If response is not JSON, use status text
          errorMessage = `HTTP ${response.status}: ${response.statusText}`;
        }
        throw new Error(errorMessage);
      }

      const result = await response.json();
      
      // Create a video URL for preview
      const videoUrl = URL.createObjectURL(file);
      
      // Create analysis object
      const analysisData: VideoAnalysis = {
        id: generateId(),
        filename: file.name,
        timestamp: result.timestamp || new Date().toISOString(),
        severity: result.severity || 'None',
        description: result.description || '',
        recommendations: result.recommendations || '',
        confidence: result.confidence || 0,
        fileSize: file.size,
        videoType: file.type,
        videoUrl,
      };

      // Save to storage
      saveAnalysis(analysisData);
      setAnalysis(analysisData);

      // If high severity, show alert (could trigger email notification here)
      if (result.severity === 'High') {
        alert('ðŸš¨ HIGH SEVERITY ALERT: Immediate attention required!');
      }
    } catch (err: any) {
      setError(err.message || 'An error occurred during analysis');
      console.error('Upload error:', err);
    } finally {
      setUploading(false);
    }
  };

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case 'High': return 'bg-red-500';
      case 'Medium': return 'bg-yellow-500';
      case 'Low': return 'bg-blue-500';
      default: return 'bg-gray-500';
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 text-white">
      {/* Navigation */}
      <nav className="border-b border-gray-700 bg-gray-800/50 backdrop-blur-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-8">
              <Link href="/" className="text-2xl font-bold bg-gradient-to-r from-blue-400 to-cyan-300 bg-clip-text text-transparent">
                ðŸ¦… HawkWatch
              </Link>
              <div className="flex space-x-4">
                <Link href="/" className="text-gray-300 hover:text-white px-3 py-2 rounded-md text-sm font-medium border-b-2 border-blue-500">
                  Dashboard
                </Link>
                <Link href="/library" className="text-gray-300 hover:text-white px-3 py-2 rounded-md text-sm font-medium">
                  Library
                </Link>
                <Link href="/statistics" className="text-gray-300 hover:text-white px-3 py-2 rounded-md text-sm font-medium">
                  Statistics
                </Link>
              </div>
            </div>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-4xl font-bold mb-2">Security Dashboard</h1>
          <p className="text-gray-400">Upload video footage for AI-powered security analysis</p>
        </div>

        {/* Upload Section */}
        <div className="bg-gray-800/50 rounded-lg border border-gray-700 p-6 mb-6">
          <h2 className="text-2xl font-semibold mb-4">Upload Video</h2>
          
          <div className="border-2 border-dashed border-gray-600 rounded-lg p-8 text-center hover:border-blue-500 transition-colors">
            <input
              type="file"
              ref={fileInputRef}
              onChange={handleFileChange}
              accept="video/*"
              className="hidden"
            />
            
            {!file ? (
              <div>
                <div className="text-6xl mb-4">ðŸ“¹</div>
                <p className="text-gray-400 mb-4">Drop your video here or click to browse</p>
                <button
                  onClick={() => fileInputRef.current?.click()}
                  className="px-6 py-3 bg-blue-600 hover:bg-blue-700 rounded-lg font-medium transition-colors"
                >
                  Select Video File
                </button>
              </div>
            ) : (
              <div>
                <div className="text-4xl mb-2">âœ“</div>
                <p className="text-gray-300 mb-2">{file.name}</p>
                <p className="text-sm text-gray-500 mb-4">
                  {(file.size / (1024 * 1024)).toFixed(2)} MB
                </p>
                <div className="flex gap-4 justify-center">
                  <button
                    onClick={() => {
                      setFile(null);
                      setAnalysis(null);
                      if (fileInputRef.current) fileInputRef.current.value = '';
                    }}
                    className="px-4 py-2 bg-gray-700 hover:bg-gray-600 rounded-lg font-medium transition-colors"
                  >
                    Change File
                  </button>
                  <button
                    onClick={handleUpload}
                    disabled={uploading}
                    className="px-6 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-blue-800 disabled:cursor-not-allowed rounded-lg font-medium transition-colors"
                  >
                    {uploading ? 'Analyzing...' : 'Analyze Video'}
                  </button>
                </div>
              </div>
            )}
          </div>

          {error && (
            <div className="mt-4 p-4 bg-red-900/50 border border-red-700 rounded-lg text-red-200">
              {error}
            </div>
          )}
        </div>

        {/* Analysis Results */}
        {analysis && (
          <div className="bg-gray-800/50 rounded-lg border border-gray-700 p-6">
            <h2 className="text-2xl font-semibold mb-4">Analysis Results</h2>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
              <div>
                <div className="flex items-center gap-3 mb-3">
                  <span className="text-sm text-gray-400">Severity:</span>
                  <span className={`px-3 py-1 rounded-full text-white font-semibold ${getSeverityColor(analysis.severity)}`}>
                    {analysis.severity}
                  </span>
                  <span className="text-sm text-gray-400">Confidence: {analysis.confidence}%</span>
                </div>
                
                <div className="mb-4">
                  <h3 className="text-lg font-medium mb-2">Description</h3>
                  <p className="text-gray-300 bg-gray-900/50 p-4 rounded-lg">
                    {analysis.description}
                  </p>
                </div>
              </div>

              <div>
                <h3 className="text-lg font-medium mb-2">Recommendations</h3>
                <p className="text-gray-300 bg-gray-900/50 p-4 rounded-lg">
                  {analysis.recommendations}
                </p>
              </div>
            </div>

            {analysis.videoUrl && (
              <div className="mb-4">
                <h3 className="text-lg font-medium mb-2">Video Preview</h3>
                <video
                  src={analysis.videoUrl}
                  controls
                  className="w-full max-w-2xl rounded-lg"
                >
                  Your browser does not support video playback.
                </video>
              </div>
            )}

            <div className="flex gap-4 mt-6">
              <Link
                href="/library"
                className="px-6 py-2 bg-blue-600 hover:bg-blue-700 rounded-lg font-medium transition-colors"
              >
                View in Library
              </Link>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
